import { Component , Input, Output, EventEmitter, ElementRef, Renderer} from '@angular/core';
import { FormInputComponent } from './form-input.component';

@Component({
  selector: 'input-group',
  templateUrl: './input-group.component.html',
  styleUrls: [  './input-group.component.css',
                './form-input.component.css',
                './input-base.component.css']
})

export class InputGroupComponent extends FormInputComponent{
    @Input() public prefix: string = null;
    @Input() public suffix: string = null;

    constructor(_el: ElementRef, _re: Renderer){
        super(_el,_re)
    }
}


