import { Component , Input, Output, EventEmitter, ElementRef, Renderer} from '@angular/core';

@Component({
  selector: 'input-box',
  templateUrl: './input-box.component.html',
  styleUrls: ['./input-box.component.css']
})

export class InputBoxComponent {
    public _checked: boolean;
    private _type: string;
    private _fill: string;
    private static readonly ValidTypes: Array<string> = ['circle','square'];
    private static readonly ValidFill: Array<string> = ['solid','dash','slash'];

    constructor(protected _el: ElementRef, protected _re: Renderer){
        this.checked = false;
        this._type = InputBoxComponent.ValidTypes[0];
        this._fill = InputBoxComponent.ValidFill[0];
    }

    @Input()
    get type() {
        return this._type;
    }

    //@Output() typeChange = new EventEmitter();
    set type(value) {
        if(InputBoxComponent.ValidTypes.indexOf(value)<0){
            value = InputBoxComponent.ValidTypes[0];
        }
        this._type = value;
   //     this.typeChange.emit(this._type);
    }

    @Input()
    get checked() {
        return this._checked;
    }

   // @Output() checkedChange = new EventEmitter();
    set checked(value) {
        this._checked = value;
      //  this.checkedChange.emit(this._checked);
    }

    @Input()
    get fill() {
        return this._fill;
    }

    //@Output() fillChange = new EventEmitter();
    set fill(value) {
        if(InputBoxComponent.ValidFill.indexOf(value)<0){
            value = InputBoxComponent.ValidFill[0];
        }
        this._fill = value;
     //   this.fillChange.emit(this._fill);
    }

    getClass(){
        var tmp = {
            checked: this.checked
        };
        tmp[this.fill] = true;
        return tmp;
    }

}


