import { Input, Output, EventEmitter, ElementRef, Renderer } from '@angular/core';
import { CharacterObject } from '../models/character'

export class ItemSkillComponent<Item> {
    @Input() public item: Item;
    private _skill: number;
    @Input() public character: CharacterObject;


    constructor(protected _el: ElementRef, protected _re: Renderer) {

    }

    @Input()
    get skill(){
        return this._skill;
    }
    @Output() public skillChange = new EventEmitter();
    set skill(value){
        this._skill = value;
        this.skillChange.emit(this.skill);
    }

}


