import { Component , Input, Output, EventEmitter, ElementRef, Renderer} from '@angular/core';

@Component({
  selector: 'computed-expression',
  template: 
`<div>
<span *ngIf="right">{{expression}}=</span>
<span *ngIf="!right">={{expression}}</span>
</div>
`,
  styleUrls: ['./computed-expression.component.css']
})

export class ComputedExpressionComponent {
    
    private _expression: string;
    private _expressionLookupTable: object;
    @Input() public right: boolean = false;

    constructor(private _el: ElementRef, private _re: Renderer){

    }

    @Input()
    get expression() {
        var expression = this._expression;
        if (this._expressionLookupTable) {
            var self = this;
            Object.keys(this._expressionLookupTable).forEach(function (key) {
                var str = self._expressionLookupTable[key].name;
                if (str) {
                    var regex = new RegExp('\\b' + key + '\\b', "g");
                    expression = expression.replace(regex, str);
                }
            })
        }
        
        expression = expression.replace("*", "")
        return expression;
    }

    set expression(value) {
        if (this._expressionLookupTable) {
            var self = this;
            Object.keys(this._expressionLookupTable).forEach(function (key) {
                var str = self._expressionLookupTable[key].name;
                if (str) {
                    var regex = new RegExp('\\b' + str + '\\b', "g");
                    value = value.replace(regex, key);
                }
            })
        }

        value = value.replace("",'*')

        this._expression = value;
    }

    @Input()
    get expressionLookupTable() {
        return this._expressionLookupTable;
    }

    set expressionLookupTable(value) {
        this._expressionLookupTable = value;
        //this.expression = this.expression; //Refresh expression, or perhaps we should subscribe to the expression
    }
}


