import { CharacterObject, CharacterSkillItem } from '../models/character';
import { CharacterAttribute } from '../models/character-attribute'
import { CharacterSkill } from '../models/character-skill'
import { CombatArt, CombatArtMoment, CombatArtTechnique } from '../models/combat-art'
import { Magic } from '../models/magic'
import { Contact } from '../models/contact';
import { Animal } from '../models/animal'
import { MeleeWeapon, RangedWeapon, Shield, Armor, Item, ItemStorage, ItemStack, Coins, CoinsStorage } from '../models/equipment/equipment';
import { Wrapper } from '../models/immutable-wrapper'

export interface ICharacterCreationFunctions{
    newCharacter() : CharacterObject;
    newAttribute() : CharacterAttribute;
    newStringValue() : Wrapper<string>;
    newMeleeWeapon(): CharacterSkillItem<MeleeWeapon>;
    newRangedWeapon(): CharacterSkillItem<RangedWeapon>;
    newShield(): CharacterSkillItem<Shield>;
    newArmor(): Armor;
    newSkill(): CharacterSkill;
    newCombatArtSkill() : CombatArt;
    newMagic(): Magic;
    newContact(): Contact;
    newCoin() : Coins;
    newCoinStorage() : CoinsStorage;
    newItem() : Item;
    newAnimal(): Animal;
    newItemStack(): ItemStack;
    newItemStorage(): ItemStorage;
}

export class CharacterCreationFunctions implements ICharacterCreationFunctions {
    public newCharacter() {
        var character = new CharacterObject();
        for (var i = 0; i < 4; ++i) {
            character.specialAbilities.push(this.newStringValue());
        }
        for (var i = 0; i < 11; ++i) {
            character.miscellaneous.push(this.newStringValue());
        }
        character.characteristics.reputation.value = CharacterObject.DefaultValuesStatic.reputation;
        character.attributes.luck.value = CharacterObject.DefaultValuesStatic.luck;

        for (var i = 0; i < 5; ++i) {
            character.equipment.meleeWeapons.push(this.newMeleeWeapon());
        }
        for (var i = 0; i < 3; ++i) {
            character.equipment.rangedWeapons.push(this.newRangedWeapon());
        }
        for (var i = 0; i < 2; ++i) {
            character.equipment.shields.push(this.newShield());
        }
        for (var i = 0; i < 7; ++i) {
            character.equipment.armor.push(this.newArmor());
        }

        for (var i = 0; i < 20; ++i) {
            character.miscellaneousSkills.push(this.newSkill())
        }

        character.combatArts.push(this.newCombatArtSkill());

        {
            var nSkills = 3;
            for (var i = 0; i < nSkills; ++i) {
                character.languageSkills.push(this.newSkill());
            }
            for (var i = 0; i < nSkills; ++i) {
                character.scriptSkills.push(this.newSkill());
            }
        }

        for (var i = 0; i < 3; ++i) {
            character.equipment.coins.push(this.newCoin())
        }

        for (var i = 0; i < 3; ++i) {
            character.equipment.coinsStorage.push(this.newCoinStorage())
        }

        for (var i = 0; i < 12; ++i) {
            character.magic.push(this.newMagic())
        }

        for (var i = 0; i < 4; ++i) {
            character.family.others.push(this.newStringValue())
        }

        for (var i = 0; i < 6; ++i) {
            character.contacts.push(this.newContact())
        }

        for (var i = 0; i < 5; ++i) {
            character.equipment.itemsCarried.push(this.newItem())
        }

        for (var i = 0; i < 12; ++i) {
            character.equipment.luggage.push(this.newItem())
        }

        for (var i = 0; i < 5; ++i) {
            character.equipment.itemsStorage.push(this.newItemStorage())
        }


        for (var i = 0; i < 2; ++i) {
            character.animals.push(this.newAnimal())
        }

        return character;
    }

    public newAttribute() {
        return new CharacterAttribute();
    }

    public newStringValue() {
        return new Wrapper<string>();
    }

    public newMeleeWeapon() {
        var weapon = new CharacterSkillItem<MeleeWeapon>();
        weapon.item = new MeleeWeapon();
        return weapon;
    }

    public newRangedWeapon() {
        var weapon = new CharacterSkillItem<RangedWeapon>();
        weapon.item = new RangedWeapon();
        return weapon;
    }

    public newShield() {
        var shield = new CharacterSkillItem<Shield>();
        shield.item = new Shield();
        return shield;
    }

    public newArmor() {
        return new Armor();
    }

    public newSkill() {
        return new CharacterSkill(null);
    }

    public newCombatArtSkill() {
        var art = new CombatArt();

        for (var i = 0; i < 2; ++i) {
            art.moments.push(new CombatArtMoment());
        }
        for (var i = 0; i < 3; ++i) {
            art.techniques.push(new CombatArtTechnique());
        }
        return art;
    }

    public newMagic() {
        return new Magic();
    }
    public newContact() {
        return new Contact();
    }

    public newCoin() {
        return new Coins();
    }
    public newCoinStorage() {
        return new CoinsStorage();
    }
    public newItem() {
        return new Item();
    }

    public newAnimal() {
        var animal = new Animal();
        for (var i = 0; i < 5; ++i) {
            animal.inventory.push(this.newItem())
        }
        for (var i = 0; i < 4; ++i) {
            animal.techniques.push(this.newStringValue())
        }
        return animal;
    }

    public newItemStack() {
        return new ItemStack();
    }
    public newItemStorage() {
        return new ItemStorage();
    }
}


