import { Component , Input, Output, EventEmitter} from '@angular/core';
import { CharacterCharacteristic } from './models/character-charactertistic';
import { InputBaseComponent } from './input-base.component';

@Component({
  selector: 'character-characteristic',
  templateUrl: './character-characteristic.component.html',
  styleUrls: [
                './form-input.component.css', 
                './input-base.component.css']
})

export class CharacterCharacteristicComponent {
    @Input() public label: string;
    @Input() public characteristic: CharacterCharacteristic;
}


