import { Component, Input, Output, EventEmitter, ElementRef, Renderer } from '@angular/core';

@Component({
    selector: 'add-remove-menu',
    templateUrl: './add-remove-menu.component.html'
})

export class AddRemoveMenuComponent {

    @Input() public array: Array<any>;
    @Input() public min: number;
    @Input() public max: number;
    @Input() public default: Function;

    constructor(private _el: ElementRef, private _re: Renderer) {

    }

    @Output() add = new EventEmitter();
    @Output() remove = new EventEmitter();

    doAdd(){
        if(this.max !== null && typeof this.max !== 'undefined'){
            if(this.array.length >= this.max){
                return;
            }
        }
        this.array.push(this.default())
        this.add.emit();
    }

    doRemove(){
        if(this.min !== null && typeof this.min !== 'undefined'){
            if(this.array.length <= this.min){
                return;
            }
        }
        this.array.pop();
        this.remove.emit();
    }
}


