import { Input, Output, EventEmitter, DoCheck } from '@angular/core';
import {primitive, createModelSchema, list, object, serializable, reference } from 'serializr';
import { myObject } from '../util/serialize-context-refs'

declare var math: any;

export interface NamedValue<Type> {
    name: string;
    value: Type;
}

export class CharacterAttribute implements NamedValue<number>{
    @serializable private _value: number = Number.NaN;
    @serializable public name: string = null;

    get value() {
        return this._value;
    }

    set value(value) {
        this._value = value;
        if(typeof value === 'number')
            this._value = Math.floor(this._value);
    }
}

export class CharacterDiceAttribute implements NamedValue<string>{
    @serializable private _value: string = null;
    @serializable public name: string = null;

    get value() {
        return this._value;
    }

    set value(value) {
        this._value = value;
    }
}

export class CharacterAttributes {
    strength: CharacterAttribute = new CharacterAttribute();
    constitution: CharacterAttribute = new CharacterAttribute();
    dexterity: CharacterAttribute = new CharacterAttribute();
    charisma: CharacterAttribute = new CharacterAttribute();
    intelligence: CharacterAttribute = new CharacterAttribute();
    will: CharacterAttribute = new CharacterAttribute();
    education: CharacterAttribute = new CharacterAttribute();
    sight: CharacterAttribute = new CharacterAttribute();
    hearing: CharacterAttribute = new CharacterAttribute();
    luck: CharacterAttribute = new CharacterAttribute();
    qadosh: CharacterAttribute = new CharacterAttribute();
    combatExperience: CharacterAttribute = new CharacterAttribute();
    extraAttributes: Array<CharacterAttribute> = new Array<CharacterAttribute>();
    eon4will: CharacterDiceAttribute = new CharacterDiceAttribute();
}

createModelSchema(CharacterAttributes, {
    strength: object(CharacterAttribute),
    constitution: object(CharacterAttribute),
    dexterity: object(CharacterAttribute),
    charisma: object(CharacterAttribute),
    intelligence: object(CharacterAttribute),
    will: object(CharacterAttribute),
    education: object(CharacterAttribute),
    sight: object(CharacterAttribute),
    hearing: object(CharacterAttribute),
    luck: object(CharacterAttribute),
    qadosh: object(CharacterAttribute),
    combatExperience: object(CharacterAttribute),
    extraAttributes: list(object(CharacterAttribute)),
    eon4will: object(CharacterDiceAttribute),
})// (context : any) => serializrConstructor(context, CharacterAttributes) )

export class CharacterInferedAttribute implements NamedValue<number> {
    @serializable public name: string;
    @serializable(myObject(CharacterAttributes)) public baseValues: CharacterAttributes;
    @serializable public min: number = Number.MIN_VALUE;
    @serializable public max: number = Number.MAX_VALUE;
    @serializable private _value: number = Number.NaN;
    @serializable private _expression: string = null;
    @serializable public ignoreExpression: boolean = false;
    @serializable(list(primitive())) private _variableNames: Array<string>;

    constructor(baseValues: CharacterAttributes) {
        this.baseValues = baseValues;
    }

    get value() {
        return this._value;
    }

    set value(value) {
        if (this.ignoreExpression) {
            this.setValue(value);
        }
    }

    get expression() {
        return this._expression;
    }

    set expression(value) {
        this._expression = value;
        var regex = new RegExp(/\b\w+\b/, "g");
        var match = null;
        this._variableNames = []

        do{
           match = regex.exec(this._expression); 
           if(match)
                this._variableNames.push(match[0])
        }while(match);
    }

    private setValue(value){
        this._value = Math.floor(value);
    }

    public updateValue() {
        if (!this.ignoreExpression) {
            var values = {

            }
            var self = this;
            if(self.baseValues){
                this._variableNames.forEach(function (key) {
                    if (self.baseValues[key] && self.baseValues[key].value !== null && typeof self.baseValues[key].value !== 'undefined')
                        values[key] = self.baseValues[key].value
                })
            }
            var val = this.compute(values);
            if (val < this.min) {
                val = this.min;
            } else if (val > this.max) {
                val = this.max;
            }
            this.setValue(val);
        }
    }

    private compute(variables): number {
        try {
            return math.eval(this.expression, variables)
        } catch (e) {
            return 0;
        }
    }
}



