import { Input, Output, EventEmitter } from '@angular/core';
import { Item } from './equipment/item'
import { Wrapper} from './immutable-wrapper'

import {primitive, list, object, serializable } from 'serializr';

export class Animal {
    @serializable public name: string = null;
    @serializable public carryingCapacity: number = null;
    @serializable public movement: number = null;
    @serializable public damage: string = null;
    @serializable(list(object(Item))) public inventory: Array<Item> = new Array<Item>();
    @serializable(list(object(Wrapper))) public techniques: Array<Wrapper<string>> = new Array<Wrapper<string>>();
}




