import { Component, ElementRef, Renderer, Input, Output, EventEmitter } from '@angular/core';
import { CharacterObject } from './models/character'
//import * as DropboxAPI from 'dropbox'
//import DropboxAPI = require('dropbox');
declare var jQuery: any;
declare var Dropbox: any;

import { CharacterCreationFunctions } from './util/character-creation-functions'

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})

//@StaticFunctionsDecorator()
export class AppComponent {
    character: CharacterObject = null;
    functionWrapper: CharacterCreationFunctions = new CharacterCreationFunctions();
    filename: string = "Karaktr";

    private static LOCAL_STORAGE_PATH = "Character"
    //client;

    constructor(protected _el: ElementRef, protected _re: Renderer) {
        this.newCharacter();
        let self = this;
        self.importCharacterFromLocalStorage();

        window.addEventListener("beforeunload", function(e){
            self.exportCharacterToLocalStorage();
         }, false);
    }

    dropboxChooser() {
        var self = this;
        var options = {

            // Required. Called when a user selects an item in the Chooser.
            success: function (files) {
                var link = files[0].link;
                //alert("Here's the file link: " + files[0].link)
                //link = link.replace("?dl=0", "?dl=1") //TODO do better...
                //console.log(link)

                jQuery.ajax({
                    url: link,
                    async: true,
                    crossDomain: true,
                    dataType: 'text',
                    success: (jsonText) => {
                        console.log(jsonText)
                        self.character = CharacterObject.deserialize(jsonText);
                    }
                })
            },

            // Optional. Called when the user closes the dialog without selecting a file
            // and does not include any parameters.
            cancel: function () {

            },

            // Optional. "preview" (default) is a preview link to the document for sharing,
            // "direct" is an expiring link to download the contents of the file. For more
            // information about link types, see Link types below.
            linkType: "direct", // or "direct"

            // Optional. A value of false (default) limits selection to a single file, while
            // true enables multiple file selection.
            multiselect: false, // or true

            // Optional. This is a list of file extensions. If specified, the user will
            // only be able to select files with these extensions. You may also specify
            // file types, such as "video" or "images" in the list. For more information,
            // see File types below. By default, all extensions are allowed.
            extensions: ['.json'],
        };
        Dropbox.choose(options);
    }

    dropboxSaver() {
        var options = {
            // Success is called once all files have been successfully added to the user's
            // Dropbox, although they may not have synced to the user's devices yet.
            success: function () {
                // Indicate to the user that the files have been saved.
                alert("Success! Files saved to your Dropbox.");
            },

            // Progress is called periodically to update the application on the progress
            // of the user's downloads. The value passed to this callback is a float
            // between 0 and 1. The progress callback is guaranteed to be called at least
            // once with the value 1.
            progress: function (progress) { },

            // Cancel is called if the user presses the Cancel button or closes the Saver.
            cancel: function () { },

            // Error is called in the event of an unexpected response from the server
            // hosting the files, such as not being able to find a file. This callback is
            // also called if there is an error on Dropbox or if the user is over quota.
            error: function (errorMessage) { alert(errorMessage) }
        };
        /*
        var url: string;
        var filename: string;
        this.client.authenticate(function () {
            this.client.writeFile('hello.txt', 'Hello, World!', function () {
                alert('File written!');
            });
        });*/

        //Dropbox.save('https://dl.dropboxusercontent.com/1/view/066vqli7gxhjl0s/Eon/Eon%20III%20Rollformul%C3%A4r/test%20%2817%29.json', 'test 18', options);
    }

    exportCharacter() {
        var jsonData = this.character.serialize();
        var a = document.createElement("a");
        var file = new Blob([jsonData], { type: 'text/json' });
        var url = URL.createObjectURL(file);
        a.href = url;
        a.download = this.filename + '.json';
        document.body.appendChild(a);
        a.click();

        setTimeout(function(){
            document.body.removeChild(a);
            URL.revokeObjectURL(url);  
        }, 100);
    }

    importCharacter() {
        var input = document.createElement('input');
        input.type = 'file';
        input.accept = ".json";
        var self = this;

        input.addEventListener('change', function (e: any) {
            var file = e.target.files[0];
            if (!file) {
                return;
            }
            var reader = new FileReader();
            reader.onload = function (e: any) {
                var contents = e.target.result;
                self.character = CharacterObject.deserialize(contents);
            };
            self.filename = file.name.replace(/\.json$/,"");
            reader.readAsText(file);
        }, false);
        input.click()
    }
    
    importCharacterFromLocalStorage(){
        var jsonData = window.localStorage.getItem(AppComponent.LOCAL_STORAGE_PATH);
        if(jsonData){
            //this.character = CharacterObject.deserialize(jsonData);
        }
    }

    exportCharacterToLocalStorage(){
        var jsonData = this.character.serialize();
        window.localStorage.setItem(AppComponent.LOCAL_STORAGE_PATH, jsonData);
    }

    newCharacter(){
        this.character = this.functionWrapper.newCharacter();
    }
}


