import { Input, Output, EventEmitter } from '@angular/core';
import { Item } from './item';

import { object, serializable } from 'serializr';

class Protection{
    @serializable public slash: number = null;
    @serializable public crush: number = null;
    @serializable public pierce: number = null;
}

export class Armor extends Item {
    @serializable public areas: string = null;
    @serializable(object(Protection)) public resistance: Protection = new Protection();
    @serializable public durability: number = null;
}


