import { Component, Input, Output, EventEmitter, ElementRef, Renderer, ContentChild, TemplateRef } from '@angular/core';
import {DamageColumns } from '../models/damage-columns'

@Component({
    selector: 'damage-grid',
    templateUrl: './damage-grid.component.html',
    styleUrls: ['./damage-grid.component.css']
})

export class DamageGridComponent {

    @Input() public damageColumns: DamageColumns;
    @Input() public header: string;
    @Input() public severDamageName: string;
    private _columns: Array<number> = [];
    private _rows: Array<number> = [];
    private _type: string;
    @Input() public labelsRight: boolean;
    private static readonly ValidTypes: Array<string> = ['circle', 'square'];

    @ContentChild("label") labelTemplate: TemplateRef<any>;
    @ContentChild("labelHeader") labelHeaderTemplate: TemplateRef<any>;

    constructor(private _el: ElementRef, private _re: Renderer) {
        this.type = DamageGridComponent.ValidTypes[0];
        this.labelsRight = false;
        this.severDamageName = null;
    }

    @Input()
    get type() {
        return this._type;
    }

    set type(value) {
        if (DamageGridComponent.ValidTypes.indexOf(value) < 0) {
            value = DamageGridComponent.ValidTypes[0];
        }
        this._type = value; //TODO perhaps revalidate the value aswell...
    }

    @Input()
    get columns() {
        return this._columns.length;
    }

    @Output() columnsArrayChange = new EventEmitter();
    set columns(value) {
        
        this._columns = [];
        for (var i = 0; i < value; ++i)
            this._columns.push(i);
        this.columnsArrayChange.emit(this.columnsArray);
    }

    @Input()
    get rows() {
        return this._rows.length;
    }

    @Output() rowsArrayChange = new EventEmitter();
    set rows(value) {
        this._rows = [];
        for (var i = 0; i < value; ++i)
            this._rows.push(i);
        this.rowsArrayChange.emit(this.rowsArray);
    }

    public get rowsArray() {   
        return this._rows;
    }

    public get columnsArray() {
        return this._columns;
    }

    getCellClass(row: number, column: number){
        var classes = { fill: false, stroke: false, dash: false}
        if( column >= this.damageColumns.columns){
            classes.fill = true;
        } else if(row * this.damageColumns.columns + column < this.damageColumns.damageSever){
            classes.stroke = true;
        } else if(row * this.damageColumns.columns + column - this.damageColumns.damageSever < this.damageColumns.damage){
            classes.dash = true;
        }

        classes[this.type] = true;
        return classes;
    }
}


