import { Input, Output, EventEmitter } from '@angular/core';
import { Item } from './item';
import { SpeedIndex } from './melee-weapon'

import { object, serializable } from 'serializr';

export class Shield extends Item {
    @serializable public damage: string = null;
    @serializable public durability: number = null;
    @serializable(object(SpeedIndex)) public speedIndex: SpeedIndex = new SpeedIndex();
    @serializable public passiveProtection: string = null;
}


