import { Component, Input, Output, EventEmitter, ElementRef, Renderer } from '@angular/core';
import { CharacterObject } from '../models/character'
import { RangedWeapon, Quiver } from '../models/equipment/ranged-weapon'
import { ItemSkillComponent } from './item-skill.component'

@Component({
    selector: 'tbody[ranged-weapon]',
    templateUrl: './ranged-weapon.component.html'
})

export class RangedWeaponComponent extends ItemSkillComponent<RangedWeapon>{
    constructor(_el: ElementRef, _re: Renderer) {
        super(_el, _re);
    }

    public newQuiver(){
        return new Quiver();
    }
}


