import { Component , Input, Output, EventEmitter} from '@angular/core';
import { CharacterStat } from './models/character-stat';
import { InputBaseComponent } from './input-base.component';

@Component({
  selector: 'character-stat-input',
  templateUrl: './character-stat-input.component.html',
  styleUrls: ['./character-stat-input.component.css',
                './form-input.component.css', 
                './input-base.component.css']
})

export class CharacterStatInputComponent {
    @Input() public label: string;
    @Input() public stat: CharacterStat;
}


