import { Component, ElementRef, Renderer, Input, Output, EventEmitter, AfterViewInit } from '@angular/core';
import { CharacterObject } from './models/character'
declare var jQuery: any;
declare var Dropbox: any;

@Component({
    selector: 'dropbox-button',
    template: ''
})

export class DropboxButtonComponent implements AfterViewInit{
    private _character: CharacterObject;
    @Output() characterChange = new EventEmitter();
    @Input()  get character(){
        return this._character;
    }
    set character(value){
        this._character = value;
        this.characterChange.emit(this.character);
    }

    constructor(private _el: ElementRef){
    }

    ngAfterViewInit(){
        var self = this;
        var options = {
            success: function (files) {
                var link = files[0].link;
                jQuery.ajax({
                    url: link,
                    async: true,
                    crossDomain: true,
                    dataType: 'text',
                    success: (jsonText) => {
                        self.character = CharacterObject.deserialize(jsonText);
                    }
                })
            },
            cancel: function () {

            },
            linkType: "direct", 
            multiselect: false, 
            extensions: ['.json'],
        };
        var button = Dropbox.createChooseButton(options);
        this._el.nativeElement.appendChild(button);
    }
}


