import { Input, Output, EventEmitter } from '@angular/core';
import { NamedValue } from './character-attribute'
import { primitive, list, object, serializable, reference, createModelSchema } from 'serializr';
import { myObject } from '../util/serialize-context-refs'

export class CharacterSkill {
    public baseValue: NamedValue<number>;
    private _value: number = null;
    public name: string = null;
    public specialization: string = null;

    constructor(baseValue: NamedValue<number>) {
        this.baseValue = baseValue;
    }

    @Input()
    public get value() {
        var sum = null;
        if (this.baseValue && this.baseValue.value) {
            sum = sum + this.baseValue.value;
        }
        if (!Number.isNaN(this._value) && this._value !== null) {
            sum = sum + this._value;
        }
        return sum;
    }

    public set value(value) {
        if (value !== null) {
            value = Number(value);
            if (this.baseValue && this.baseValue.value) {
                this._value = value - this.baseValue.value;
            } else {
                this._value = value;
            }
            if (this._value < 1) {
                this._value = 0;
            }
            this._value = Math.floor(this._value);
        } else {
            this._value = value;
        }
    }
}

createModelSchema(CharacterSkill, {
    _value: primitive(),
    name: primitive(),
    specialization: primitive(),
    baseValue: myObject(null)
})//, (context : any) => serializrInterface(context, ['baseValue']) )


