import { Input, Output, EventEmitter } from '@angular/core';

import {primitive, list, object, serializable, reference } from 'serializr';


export class CombatArtTechnique {
    @serializable public name: string = null;
    @serializable public description: string = null;
    @serializable public cost: number = null;
}

export class CombatArtMoment {
    @serializable public name: string = null;
    @serializable public difficulty: string = null;
    @serializable public relation: string = null;
}

export class CombatArt {
    @serializable public name: string = null;
    @serializable public value: number = null;
    
    @serializable(list(object(CombatArtMoment))) public moments: Array<CombatArtMoment> = new Array<CombatArtMoment>();
    @serializable(list(object(CombatArtTechnique))) public techniques: Array<CombatArtTechnique> = new Array<CombatArtTechnique>();
}


