import { Input, Output, EventEmitter } from '@angular/core';
import { Item } from './item';

import { object, serializable } from 'serializr';

export class MeleeDamage{
    @serializable public slash: string = null;
    @serializable public crush: string = null;
    @serializable public pierce: string = null;
}
export class SpeedIndex{
    @serializable public opening: number = null;
    @serializable public engaged: number = null;
}

export class MeleeWeapon extends Item {
    @serializable public grip: string = null;
    @serializable(object(MeleeDamage)) public damage: MeleeDamage = new MeleeDamage();
    @serializable public durability: number = null;
    @serializable(object(SpeedIndex)) public speedIndex: SpeedIndex = new SpeedIndex();
}


