import { Input, Output, EventEmitter } from '@angular/core';

import { CharacterStat } from './character-stat';
import { CharacterCharacteristic } from './character-charactertistic'
import { CharacterAttributes, CharacterAttribute, CharacterInferedAttribute } from './character-attribute'
import { CombatArt } from './combat-art'
import { MeleeWeapon, RangedWeapon, Shield, Armor, Item, ItemStorage, ItemStack, Coins, CoinsStorage } from './equipment/equipment'
import { MeleeDamage } from './equipment/melee-weapon'
import { Animal } from './animal'
import { CharacterSkill } from './character-skill'
import { Magic } from './magic'
import { Contact } from './contact'
import { Wrapper } from './immutable-wrapper'
import { DamageColumns } from './damage-columns'

import { createModelSchema, primitive, reference, list, custom, object, identifier, serialize, deserialize, getDefaultModelSchema, serializable } from 'serializr';

import { myObject } from '../util/serialize-context-refs'
declare let CircularJSON: any;

export class CharacterSkillItem<T>{
    @serializable public skill: Wrapper<number> = null;
    public item: T = null;
}
export class CharacterSkillMelee extends CharacterSkillItem<MeleeWeapon>{}
createModelSchema(CharacterSkillMelee, { item: object(MeleeWeapon)});
export class CharacterSkillRanged extends CharacterSkillItem<RangedWeapon>{}
createModelSchema(CharacterSkillRanged, { item: object(RangedWeapon)});
export class CharacterSkillShield extends CharacterSkillItem<Shield>{}
createModelSchema(CharacterSkillShield, { item: object(Shield)});

class Insensitivity{
    @serializable public value: number
    
    constructor(){
        this.value = 0;
    }
}
createModelSchema(Insensitivity, {
    value: primitive()
});

class Insensitivities{
    @serializable public exposure: Insensitivity = new Insensitivity();
    @serializable public violence: Insensitivity = new Insensitivity();
    @serializable public supernatural: Insensitivity = new Insensitivity();
    
    constructor(){
    }
}
createModelSchema(Insensitivities, {
    exposure: object(Insensitivity),
    violence: object(Insensitivity),
    supernatural: object(Insensitivity)
});


class CharacterCharacteristics{
    public loyalty: CharacterStat = new CharacterStat();
    public honor: CharacterStat = new CharacterStat();
    public lust: CharacterStat = new CharacterStat();
    public aggression: CharacterStat = new CharacterStat();
    public faith: CharacterStat = new CharacterStat();
    public generosity: CharacterStat = new CharacterStat();
    public reputation: CharacterStat = new CharacterStat();
    public primary: CharacterCharacteristic = new CharacterCharacteristic();
    public secondary: CharacterCharacteristic = new CharacterCharacteristic();
}
createModelSchema(CharacterCharacteristics, {
    loyalty: object(CharacterStat),
    honor: object(CharacterStat),
    lust: object(CharacterStat),
    aggression: object(CharacterStat),
    faith: object(CharacterStat),
    generosity: object(CharacterStat),
    reputation: object(CharacterStat),
    primary: object(CharacterCharacteristic),
    secondary: object(CharacterCharacteristic)
});

class InferedAttributes{
    public speed: CharacterInferedAttribute;
    public initiative: CharacterInferedAttribute;
    public insight: CharacterInferedAttribute;
    public chockValue: CharacterInferedAttribute;
    public carryingCapacity: CharacterInferedAttribute;
}
createModelSchema(InferedAttributes, {
    speed: myObject(CharacterInferedAttribute),
    initiative: myObject(CharacterInferedAttribute),
    insight: myObject(CharacterInferedAttribute),
    chockValue: myObject(CharacterInferedAttribute),
    carryingCapacity: myObject(CharacterInferedAttribute)
});
class InferedBaseSkills{
    public constitution: CharacterInferedAttribute;
    public dexterity: CharacterInferedAttribute;
    public intelligence: CharacterInferedAttribute;
    public education: CharacterInferedAttribute;
    public charismaIntelligence: CharacterInferedAttribute;
    public dexterityIntelligence: CharacterInferedAttribute;
    public sightIntelligence: CharacterInferedAttribute;
    public nativeLanguage: CharacterInferedAttribute
}
createModelSchema(InferedBaseSkills, {
    constitution: myObject(CharacterInferedAttribute),
    dexterity: myObject(CharacterInferedAttribute),
    intelligence: myObject(CharacterInferedAttribute),
    education: myObject(CharacterInferedAttribute),
    charismaIntelligence: myObject(CharacterInferedAttribute),
    dexterityIntelligence: myObject(CharacterInferedAttribute),
    sightIntelligence: myObject(CharacterInferedAttribute),
    nativeLanguage: myObject(CharacterInferedAttribute)
});
class BaseSkills{
    public storyTelling: CharacterSkill;
    public dance: CharacterSkill;
    public sleightOfHand: CharacterSkill;
    public fishing: CharacterSkill;
    public seduction: CharacterSkill;
    public interogate: CharacterSkill;
    public jest: CharacterSkill;
    public hide: CharacterSkill;
    public trade: CharacterSkill;
    public jump: CharacterSkill;
    public throw: CharacterSkill;
    public climb: CharacterSkill;
    public driveWagon: CharacterSkill;
    public leadership: CharacterSkill;
    public marsch: CharacterSkill;
    public cooking: CharacterSkill;
    public occultism: CharacterSkill;
    public ride: CharacterSkill;
    public sing: CharacterSkill;
    public ski: CharacterSkill;
    public acting: CharacterSkill;
    public brawl: CharacterSkill;
    public sneak: CharacterSkill;
    public scout: CharacterSkill;
    public gambling: CharacterSkill;
    public tracking: CharacterSkill;
    public drinking: CharacterSkill;
    public search: CharacterSkill;
    public educate: CharacterSkill;
    public avoid: CharacterSkill;
    public appraise: CharacterSkill;
    public survival: CharacterSkill;
    public persuade: CharacterSkill;
}
createModelSchema(BaseSkills, {
    storyTelling: myObject(CharacterSkill),
    dance: object(CharacterSkill),
    sleightOfHand: object(CharacterSkill),
    fishing: object(CharacterSkill),
    seduction: object(CharacterSkill),
    interogate: object(CharacterSkill),
    jest: object(CharacterSkill),
    hide: object(CharacterSkill),
    trade: object(CharacterSkill),
    jump: object(CharacterSkill),
    throw: object(CharacterSkill),
    climb: object(CharacterSkill),
    driveWagon: object(CharacterSkill),
    leadership: object(CharacterSkill),
    marsch: object(CharacterSkill),
    cooking: object(CharacterSkill),
    occultism: object(CharacterSkill),
    ride: object(CharacterSkill),
    sing: object(CharacterSkill),
    ski: object(CharacterSkill),
    acting: object(CharacterSkill),
    brawl: object(CharacterSkill),
    sneak: object(CharacterSkill),
    scout: object(CharacterSkill),
    gambling: object(CharacterSkill),
    tracking: object(CharacterSkill),
    drinking: object(CharacterSkill),
    search: object(CharacterSkill),
    educate: object(CharacterSkill),
    avoid: object(CharacterSkill),
    appraise: object(CharacterSkill),
    survival: object(CharacterSkill),
    persuade: object(CharacterSkill)
});
class Family{
    mother: string = null;
    father: string = null;
    others: Array<Wrapper<string>> = [];
}

createModelSchema(Family, {
    mother: primitive(),
    father: primitive(),
    others: list(object(Wrapper))
});
class Equipment{
    public meleeWeapons: Array<CharacterSkillItem<MeleeWeapon>> = [];
    public rangedWeapons: Array<CharacterSkillItem<RangedWeapon>> = [];
    public shields: Array<CharacterSkillItem<Shield>> = [];
    public armor: Array<Armor> = [];
    public itemsCarried: Array<Item> = [];
    public itemsStorage: Array<ItemStorage> = [];
    public luggage: Array<Item> = [];
    public coins: Array<Coins> = [];
    public coinsStorage: Array<CoinsStorage> = [];
}
createModelSchema(Equipment, {
    meleeWeapons: list(object(CharacterSkillMelee)),
    rangedWeapons:  list(object(CharacterSkillRanged)),
    shields:  list(object(CharacterSkillShield)),
    armor:  list(object(Armor)),
    itemsCarried: list(object(Item)),
    itemsStorage: list(object(ItemStorage)),
    luggage: list(object(Item)),
    coins: list(object(Coins)),
    coinsStorage: list(object(CoinsStorage))
});
export class CharacterObject {
    public player: string = null;
    public gameMaster: string = null;
    public name: string = null;
    public profession: string = null;
    public race: string = null;
    public gender: string = null;
    public age: string = null;
    public height: string = null;
    public weight: string = null;
    public body: string = null;
    public homestead: string = null;
    public religion: string = null;
    public handDominance: string = null;

    public attributes: CharacterAttributes = new CharacterAttributes();
    public characteristics: CharacterCharacteristics = new CharacterCharacteristics();
    public specialAbilities: Array<Wrapper<string>> = new Array<Wrapper<string>>();
    public baseDamage: MeleeDamage = new MeleeDamage();
    public exhaustion: DamageColumns = new DamageColumns();
    public trauma: DamageColumns = new DamageColumns();
    public pain: DamageColumns = new DamageColumns();
    public bleeding: DamageColumns = new DamageColumns();
    public bloodLoss: DamageColumns = new DamageColumns();
    public inferedAttributes: InferedAttributes = new InferedAttributes();
    public inferedBaseSkills: InferedBaseSkills = new InferedBaseSkills();
    public skills: BaseSkills = new BaseSkills();
    public languageSkills: Array<CharacterSkill> = new Array<CharacterSkill>();
    public scriptSkills: Array<CharacterSkill> = new Array<CharacterSkill>();
    public miscellaneousSkills: Array<CharacterSkill> = new Array<CharacterSkill>();
    public combatArts: Array<CombatArt> = new Array<CombatArt>();
    public miscellaneous: Array<Wrapper<string>> = new Array<Wrapper<string>>();
    public magic: Array<Magic> = new Array<Magic>();
    public contacts: Array<Contact> = new Array<Contact>();
    public family: Family = new Family();
    public animals: Array<Animal> = new Array<Animal>();
    public equipment: Equipment = new Equipment();
    public rations: ItemStack = new ItemStack();

    public insensitivities : Insensitivities = new Insensitivities();

    public focus: number = 10;
    public wellBeing: number = 10;

    private static readonly insentivityStep = 5;
    static readonly maxInsensitivitylevel: number = 5;
    private static readonly _insensitivityThresholds: Array<number> =[5,10,15,20,25]//Array<number>(CharacterObject.maxInsensitivitylevel).map((i, j)=> {console.log(j); return j*CharacterObject.insentivityStep});

    
    static readonly maxWellBeing: number = 10;
    private static readonly _maxWellBeingArray: Array<number> = Array<number>(CharacterObject.maxWellBeing);

    public static readonly DefaultValuesStatic: {
        luck: number;
        reputation: number;
      } = {
        luck: 11,
        reputation: 5
      };
    

    constructor() {
        var self = this;

        this.inferedAttributes = {
            speed: new CharacterInferedAttribute(this.attributes),
            initiative: new CharacterInferedAttribute(this.attributes),
            insight: new CharacterInferedAttribute(this.attributes),
            chockValue: new CharacterInferedAttribute(this.attributes),
            carryingCapacity: new CharacterInferedAttribute(this.attributes)
        }
        this.inferedAttributes.speed.expression = "(dexterity+constitution+10)/4";
        this.inferedAttributes.initiative.expression = "(dexterity/2 + combatExperience)/2";
        this.inferedAttributes.insight.expression = "(intelligence + sight)/10 + combatExperience/2";
        this.inferedAttributes.chockValue.expression = "(strength + constitution + will)/3";
        this.inferedAttributes.carryingCapacity.expression = "(strength + constitution)/2";

        this.inferedAttributes.speed.min =
            this.inferedAttributes.initiative.min =
            this.inferedAttributes.insight.min =
            this.inferedAttributes.chockValue.min =
            this.inferedAttributes.carryingCapacity.min = 0;

        this.inferedBaseSkills = {
            constitution: new CharacterInferedAttribute(this.attributes),
            dexterity: new CharacterInferedAttribute(this.attributes),
            intelligence: new CharacterInferedAttribute(this.attributes),
            education: new CharacterInferedAttribute(this.attributes),

            charismaIntelligence: new CharacterInferedAttribute(this.attributes),
            dexterityIntelligence: new CharacterInferedAttribute(this.attributes),
            sightIntelligence: new CharacterInferedAttribute(this.attributes),

            nativeLanguage: new CharacterInferedAttribute(this.attributes)
        }

        this.inferedBaseSkills.constitution.expression = "constitution/2";
        this.inferedBaseSkills.dexterity.expression = "dexterity/2";
        this.inferedBaseSkills.intelligence.expression = "intelligence/2";
        this.inferedBaseSkills.education.expression = "education/2";
        this.inferedBaseSkills.charismaIntelligence.expression = "(charisma+intelligence)/4";
        this.inferedBaseSkills.dexterityIntelligence.expression = "(dexterity+intelligence)/4";
        this.inferedBaseSkills.sightIntelligence.expression = "(sight+intelligence)/4";
        this.inferedBaseSkills.nativeLanguage.expression = "intelligence";

        this.inferedBaseSkills.constitution.min =
            this.inferedBaseSkills.dexterity.min =
            this.inferedBaseSkills.intelligence.min =
            this.inferedBaseSkills.education.min =
            this.inferedBaseSkills.charismaIntelligence.min =
            this.inferedBaseSkills.dexterityIntelligence.min =
            this.inferedBaseSkills.sightIntelligence.min =
            this.inferedBaseSkills.nativeLanguage.min = 5;

        this.skills = {
            storyTelling: new CharacterSkill(this.inferedBaseSkills.charismaIntelligence),
            dance: new CharacterSkill(this.inferedBaseSkills.dexterityIntelligence),
            sleightOfHand: new CharacterSkill(this.inferedBaseSkills.dexterity),
            fishing: new CharacterSkill(this.inferedBaseSkills.intelligence),
            seduction: new CharacterSkill(this.inferedBaseSkills.charismaIntelligence),
            interogate: new CharacterSkill(this.inferedBaseSkills.intelligence),
            jest: new CharacterSkill(this.inferedBaseSkills.dexterityIntelligence),
            hide: new CharacterSkill(this.inferedBaseSkills.dexterityIntelligence),
            trade: new CharacterSkill(this.inferedBaseSkills.charismaIntelligence),
            jump: new CharacterSkill(this.inferedBaseSkills.dexterity),
            throw: new CharacterSkill(this.inferedBaseSkills.dexterity),
            climb: new CharacterSkill(this.inferedBaseSkills.dexterity),
            driveWagon: new CharacterSkill(this.inferedBaseSkills.dexterity),
            leadership: new CharacterSkill(this.inferedBaseSkills.charismaIntelligence),
            marsch: new CharacterSkill(this.inferedBaseSkills.constitution),
            cooking: new CharacterSkill(this.inferedBaseSkills.intelligence),
            occultism: new CharacterSkill(this.inferedBaseSkills.education),
            ride: new CharacterSkill(this.inferedBaseSkills.dexterity),
            sing: new CharacterSkill(this.inferedBaseSkills.charismaIntelligence),
            ski: new CharacterSkill(this.inferedBaseSkills.dexterity),
            acting: new CharacterSkill(this.inferedBaseSkills.charismaIntelligence),
            brawl: new CharacterSkill(this.inferedBaseSkills.dexterity),
            sneak: new CharacterSkill(this.inferedBaseSkills.dexterity),
            scout: new CharacterSkill(this.inferedBaseSkills.sightIntelligence),
            gambling: new CharacterSkill(this.inferedBaseSkills.intelligence),
            tracking: new CharacterSkill(this.inferedBaseSkills.sightIntelligence),
            drinking: new CharacterSkill(this.inferedBaseSkills.constitution),
            search: new CharacterSkill(this.inferedBaseSkills.sightIntelligence),
            educate: new CharacterSkill(this.inferedBaseSkills.intelligence),
            avoid: new CharacterSkill(this.inferedBaseSkills.dexterity),
            appraise: new CharacterSkill(this.inferedBaseSkills.education),
            survival: new CharacterSkill(this.inferedBaseSkills.intelligence),
            persuade: new CharacterSkill(this.inferedBaseSkills.charismaIntelligence)
        }
    }

    serialize() {
        return  CircularJSON.stringify(this); //serialize(this)
    }

    get insensitivityThresholds(){
        return CharacterObject._insensitivityThresholds;
    }

    get maxInsensitivitylevel(){
        return CharacterObject.maxInsensitivitylevel;
    }

    get maxWellBeingArray(){
        return CharacterObject._maxWellBeingArray;
    }

    get maxWellBeing(){
        return CharacterObject.maxWellBeing;
    }


    static deserialize(json: any) {
        let character = deserialize<CharacterObject>(CharacterObject, CircularJSON.parse(json));
        return character;
    }
}

createModelSchema(CharacterObject, {
    player: primitive(),
    gameMaster: primitive(),
    name: primitive(),
    profession: primitive(),
    race: primitive(),
    gender: primitive(),
    age: primitive(),
    height: primitive(),
    weight: primitive(),
    body: primitive(),
    homestead: primitive(),
    religion: primitive(),
    focus: primitive(),
    wellBeing: primitive(),
    handDominance: primitive(),
    attributes: myObject(CharacterAttributes),
    characteristics: object(CharacterCharacteristics),
    specialAbilities: list(object(Wrapper)),
    baseDamage: object(MeleeDamage),
    exhaustion: object(DamageColumns),
    trauma: object(DamageColumns),
    pain: object(DamageColumns),
    bleeding: object(DamageColumns),
    bloodLoss: object(DamageColumns),
    inferedAttributes: object(InferedAttributes),
    inferedBaseSkills: object(InferedBaseSkills),
    skills: object(BaseSkills),
    languageSkills: list(object(CharacterSkill)),
    scriptSkills: list(object(CharacterSkill)),
    miscellaneousSkills: list(object(CharacterSkill)),
    combatArts: list(object(CombatArt)),
    miscellaneous: list(object(Wrapper)),
    magic: list(object(Magic)),
    contacts: list(object(Contact)),
    family: object(Family),
    animals: list(object(Animal)),
    equipment: object(Equipment),
    rations: object(ItemStack),
    insensitivities: object(Insensitivities)
});


