import { Component, Input, Output, EventEmitter, ElementRef, Renderer, AfterViewInit } from '@angular/core';
import { InputBaseComponent } from './input-base.component';

@Component({
    selector: 'form-input',
    templateUrl: './form-input.component.html',
    styleUrls: ['./form-input.component.css', './input-base.component.css']
})

export class FormInputComponent extends InputBaseComponent implements AfterViewInit {
    private _label: string;
    private _labelEditable: boolean;

    constructor(_el: ElementRef, _re: Renderer) {
        super(_el, _re)
        this._labelEditable = false;
    }

    ngAfterViewInit() {
        var label = this._el.nativeElement.getElementsByTagName('label');
        var self = this;
        for(var i = 0;i<label.length;++i){
        label[i].addEventListener('input', function (e) {
            if(e.target.innerText !== self.label){
                self.label = e.target.innerText;
            }
        });
        }
    }

    @Input()
    get label() {
        return this._label;
    }

    @Output() labelChange = new EventEmitter();
    set label(value) {
        this._label = value;
        this.labelChange.emit(this._label);
    }

    @Input()
    get labelEditable() {
        return this._labelEditable;
    }

    set labelEditable(value) {
        this._labelEditable = value;
    }
}


