import { Directive, ElementRef, Renderer, AfterViewInit, Input, Output, EventEmitter } from '@angular/core';
import { FormInputComponent } from '../form-input.component';

declare var jQuery: any;

@Directive({
  selector: '[aligned]',
  host: {

  }
})

export class AlignedDirective implements AfterViewInit {
  @Input() public selector = "label";
  private observer: MutationObserver;
  private _bodyObserver: MutationObserver;

  constructor(private _el: ElementRef, private _re: Renderer) {

  }

  ngAfterViewInit() {
    var self = this;
    this.observer = new MutationObserver(mutations => {
      var recompute = false;
      mutations.forEach(function (mutation) {
        if (jQuery(mutation.addedNodes).find(self.selector).length > 0 ||
          jQuery(mutation.removedNodes).find(self.selector).length > 0 ||
          mutation.attributeName === "style") {
          recompute = true;
        }
      });

      if (recompute) {
        self.recomputeLongestLabel()
      }
    });

    self.recomputeLongestLabel()

    var recompute = function(){
      self.recomputeLongestLabel();
    }

    window.matchMedia('print').addListener(recompute);
    window.matchMedia('screen').addListener(recompute);

    setTimeout(function () {
      self.recomputeLongestLabel()
    }, 100);
  }

  ngOnDestroy(){
    this.observer.disconnect();
  }

  private recomputeLongestLabel() {

    var config = {
      childList: true,
      attributes: true,
      characterData: false,
      attributeFilter: ["style"],
      subtree: true,
      attributeOldValue: false,
      characterDataOldValue: false
    };

    //Turn of observr so we don't trigger it while updating the width
    this.observer.disconnect();
    var labels = jQuery(this._el.nativeElement).find(this.selector)
    //Clear set widths
    labels.css('width', '');
    var maxWidth = Math.max.apply(Math, labels.map(function () { return jQuery(this).width(); }).get());
    labels.width(maxWidth);

    this.observer.observe(this._el.nativeElement, config);
    var self = this;

  }
}


