import { Component, Input, Output, EventEmitter, ElementRef, Renderer } from '@angular/core';

@Component({
    selector: 'increment-decrement-menu',
    template: `
<div>
  <button class="fa fa-plus btn btn-default" type="button" (click)="doIncrement()"></button>
  <button class="fa fa-minus btn btn-default" type="button" (click)="doDecrement()"></button>
</div>`
})

export class IncrementDecrementMenuComponent {

    private _value: number;
    @Input() public min: number;
    @Input() public max: number;

    constructor(private _el: ElementRef, private _re: Renderer) {

    }
    @Input()
    get value() {
        return this._value;
    }

    @Output() valueChange = new EventEmitter();
    set value(value) {
        this._value = value;
        this.valueChange.emit(this.value);
    }


    @Output() increment = new EventEmitter();
    @Output() decrement = new EventEmitter();

    doIncrement() {
        if (this.max !== null && typeof this.max !== 'undefined') {
            if (this.value >= this.max) {
                return;
            }
        }
        this.value = this.value + 1;
        this.increment.emit();
    }

    doDecrement() {
        if (this.min !== null && typeof this.min !== 'undefined') {
            if (this.value <= this.min) {
                return;
            }
        }
        this.value = this.value - 1;
        this.decrement.emit();
    }
}


