import { Component , Input, Output, EventEmitter, ElementRef, Renderer} from '@angular/core';
import { ItemStack } from '../models/equipment/item-stack'


@Component({
    selector: 'rations-tracker',
    templateUrl: './rations-tracker.component.html',
    styleUrls: ['./rations-tracker.component.css']
})


export class RationsTrackerComponent{
    @Input() item: ItemStack;
    static readonly maxRations: number = 14;
    private static readonly _maxRationsArray: Array<number> = Array<number>(RationsTrackerComponent.maxRations);

    constructor(protected _el: ElementRef, protected _re: Renderer){
    }
    get maxRationsArray(){
        return RationsTrackerComponent._maxRationsArray;
    }
    get maxRations(){
        return RationsTrackerComponent._maxRationsArray.length;
    }
}


