import {Pipe, PipeTransform} from '@angular/core';

@Pipe({name: 'arrayConstructor'})
export class ArrayConstructor implements PipeTransform {
  transform(value, args:string[]) : any {
    let res = [];
    for (let i = 0; i < Math.floor(value); i++) {
        res.push(i);
      }
      return res;
  }
}


