import { Input, Output, EventEmitter } from '@angular/core';
import { ItemStack } from './item-stack';

import { serializable } from 'serializr';

export class Coins extends ItemStack{
    @serializable public worth: number = null;
}

export class CoinsStorage extends Coins{
    @serializable public location: string = null;
}



