import { Component, Input, Output, EventEmitter, ElementRef, Renderer } from '@angular/core';
import { InputGroupComponent } from './input-group.component';
import { ComputedExpressionComponent } from './computed-expression.component'
import { CharacterInferedAttribute } from './models/character-attribute'

@Component({
    selector: 'input-computed',
    templateUrl: './input-computed.component.html',
    styleUrls: ['./input-computed.component.css',
        './input-group.component.css',
        './form-input.component.css',
        './input-base.component.css']
})

export class InputComputedComponent {

    @Input() public prefix: string = null;
    @Input() public suffix: string = null;
    @Input() public computationBase: CharacterInferedAttribute;
    private _min: number;
    expressionVisible: boolean;
    @Input() public label: string;

    constructor(protected _el: ElementRef, protected _re: Renderer) {
        this.expressionVisible = false;
        var self = this;
    }

    @Input()
    get expression() {
        var expression = this.computationBase.expression;
        if (this.computationBase.baseValues) {
            var self = this;
            Object.keys(this.computationBase.baseValues).forEach(function (key) {
                var str = self.computationBase.baseValues[key].name;
                if (str) {
                    var regex = new RegExp('\\b' + key + '\\b', "g");
                    expression = expression.replace(regex, str);
                }
            })
        }
        return expression;
    }

    @Output() expressionChange = new EventEmitter();
    set expression(value) {
        if (this.computationBase.baseValues) {
            var self = this;
            Object.keys(this.computationBase.baseValues).forEach(function (key) {
                var str = self.computationBase.baseValues[key].name;
                if (str) {
                    var regex = new RegExp('\\b' + str + '\\b', "g");
                    value = value.replace(regex, key);
                }
            })
        }

        this.computationBase.expression = value;
        this.expressionChange.emit(this.computationBase.expression);
    }

    showExpression() {
        this.expressionVisible = true;
    }

    hideExpression() {
        this.expressionVisible = false;
    }
}


