import { Component , Input, Output, EventEmitter, ElementRef, Renderer} from '@angular/core';

@Component({
  selector: 'input-base',
  templateUrl: './input-base.component.html',
  styleUrls: ['./input-base.component.css']
})

export class InputBaseComponent {
    private _value: string = null;
    @Input() public readonly: boolean = false;
    private _type: string;
    private static readonly ValidTypes: Array<string> = ['text','number'];

    constructor(protected _el: ElementRef, protected _re: Renderer){
        this._type = InputBaseComponent.ValidTypes[0];
    }

    @Input()
    get type() {
        return this._type;
    }

    set type(value) {
        if(InputBaseComponent.ValidTypes.indexOf(value)<0){
            value = InputBaseComponent.ValidTypes[0];
        }
        this._type = value;
    }

    @Input()
    get value() {
        return this._value;
    }

    @Output() valueChange = new EventEmitter();
    set value(value) {
        this._value = value;
        this.valueChange.emit(this._value);
    }
}


