import { Component , Input, Output, EventEmitter, ElementRef, Renderer} from '@angular/core';
import { CombatArt,CombatArtMoment,CombatArtTechnique } from '../models/combat-art'

@Component({
  selector: 'combat-art',
  templateUrl: './combat-art.component.html',
  styleUrls: ['./combat-art.component.css']
})

export class CombatArtComponent {
    @Input() public combatArt: CombatArt;

    newTechnique(){
        return new CombatArtTechnique();
    }

    newMoment(){
        return new CombatArtMoment();
    }
}


