import { NgModule, } from '@angular/core';
import { DragulaModule } from 'ng2-dragula';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import { SortablejsModule } from 'angular-sortablejs';

import { AppComponent } from './app.component';
import { DropboxButtonComponent } from './dropbox-button.component';
import { CharacterSheetComponent } from './character-sheet.component';
import { MeleeWeaponComponent } from './page1/melee-weapon.component';
import { RangedWeaponComponent } from './page1/ranged-weapon.component';
import { ShieldComponent } from './page1/shield.component';
import { ArmorComponent } from './page1/armor.component';
import { SpeedIndexComponent } from './page1/speed-index.component';
import { DamageGridComponent } from './page1/damage-grid.component';

import { CombatArtComponent } from './page2/combat-art.component';

import { RationsTrackerComponent } from './page3/rations-tracker.component';

import { AddRemoveMenuComponent } from './add-remove-menu.component';
import { IncrementDecrementMenuComponent } from './increment-decrement-menu.component';
import { InputBaseComponent } from './input-base.component';
import { FormInputComponent } from './form-input.component';
import { InputGroupComponent } from './input-group.component';
import { CharacterStatInputComponent } from './character-stat-input.component';
import { CharacterCharacteristicComponent } from './character-characteristic.component';
import { ComputedExpressionComponent } from './computed-expression.component';
import { InputComputedComponent } from './input-computed.component';
import { InputBoxComponent } from './input-box.component';

import { AlignedDirective } from './directives/aligned.directive';
import { FocusInputsDirective } from './directives/focus-inputs.directive';
import { LocalizationDirective} from './directives/localization.directive';

import { Autosize } from './directives/autosize.directive';


import { JsonCircularPipe } from './pipes/json-circular.pipe'
import { ArrayConstructor } from './pipes/array-constructor.pipe'
import { FloorPipe, CeilPipe } from 'angular-pipes'

@NgModule({
  declarations: [
    AppComponent, DropboxButtonComponent, CharacterSheetComponent,

    MeleeWeaponComponent, SpeedIndexComponent,
    RangedWeaponComponent, ShieldComponent, ArmorComponent, DamageGridComponent,

    CombatArtComponent,

    RationsTrackerComponent,

    FormInputComponent, CharacterStatInputComponent, CharacterCharacteristicComponent, InputGroupComponent,
    InputBaseComponent, ComputedExpressionComponent, InputComputedComponent, AddRemoveMenuComponent,
    InputBoxComponent, IncrementDecrementMenuComponent,

    AlignedDirective, FocusInputsDirective, Autosize, LocalizationDirective,

    JsonCircularPipe, ArrayConstructor, FloorPipe, CeilPipe
  ],
  imports: [
    DragulaModule,
    BrowserModule,
    FormsModule,
    SortablejsModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



