
import { Component, Input, Output, EventEmitter, ElementRef, Renderer } from '@angular/core';

import { CharacterObject } from '../models/character'

@Component({
    selector: 'speed-index',
    templateUrl: './speed-index.component.html',
    styleUrls: [
        '../input-group.component.css',
        '../form-input.component.css',
        '../input-base.component.css',
        './speed-index.component.css',]
})

export class SpeedIndexComponent {
    
    private _value1: number = null;
    private _value2: number = null;
    @Input() public character: CharacterObject;

    constructor(private _el: ElementRef, private _re: Renderer) {
        
    }

    @Input()
    get value1(){
        return this._value1;
    }
    @Output() public value1Change = new EventEmitter();
    set value1(value){
        this._value1 = value;
        this.value1Change.emit(this.value1);
    }
    
    @Input()
    get value2(){
        return this._value2;
    }
    @Output() public value2Change = new EventEmitter();
    set value2(value){
        this._value2 = value;
        this.value2Change.emit(this.value2);
    }

    public get value1Sum() {
        return this.getValueSum(this.value1);
    }

    public get value2Sum() {
        return this.getValueSum(this.value2);
    }

    public set value1Sum(value) {
        this.value1 = this.setValueSum(value);
    }

    public set value2Sum(value) {
        this.value2 = this.setValueSum(value);
    }

    private getValueSum(value){
        if(value===null)
            return null;
        return value + this.character.inferedAttributes.initiative.value;
    }

    private setValueSum(value){
        if(value===null)
            return null;
        return value - this.character.inferedAttributes.initiative.value;
    }
}


